.menu{
  display: flex;
  flex-direction: column;

  padding-top: 77px;

  .logo{
    display: flex;
    justify-content: space-between;

    padding-left: 36px;
    padding-bottom: 20px;

    .logoTitle{
      display: flex;
      align-items: center;

      img{
        padding-right: 10px;
      }
      p{
        padding-right: 62px;
      }
    }
    img{
      padding-right: 20px;
    }
  }

  .sideIcons{
    display: flex;
    flex-direction: column;
    gap: 30px;

    padding-bottom: 35px;
    border-bottom: 1px solid white;

    img{
      padding-left: 36px;
    }

    div{
      display: flex;
      align-items: center;
      gap: 10px;

      height: 54px;
    }
  }
  .active{
    border-right: 4px solid #0085FF;

    background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%), rgba(255, 255, 255, 0.3);
    backdrop-filter: blur(30px);

    h2{
      color: #0085FF;
      text-shadow: 0px 0px 20px #0085FF;
    }

    img{
      padding-left: 36px;
    }
  }
  .myLists{
    display: flex;
    flex-direction: column;
    gap: 20px;

    margin-left: 36px;
    padding-right: 10px;
    padding-top: 20px;
    border-bottom: 1px solid white;
    padding-bottom: 20px;
  }

  .myTags{
    margin-left: 36px;
    padding-right: 10px;
    padding-top: 20px;

    .tags{
      display: flex;
      flex-wrap: wrap;
      padding-top: 20px;

      .label{
        display: flex;
        align-items: center;
        gap: 10px;

        height: 14px;

        padding: 2px 3px;
        margin: 5px 2px;

        color: white;
        text-align: center;

        background: #5241BF;
        border-radius: 4px;

        h5{
          font-weight: normal;
        }
      }
    }
  }
}
